YAAA,CACA,kBA+c8B,CA9c9B,qBA8cmC,CA7cnC,kBA6c0C,CA5c1C,OANM,CAmdA,QAAA,CAEA,iEACE,YAAA,CACA,cAAA,CACA,UAAA,CAGF,iEACE,0BAAA,CACA,iBAAA,CACA,eAAA,CACA,cAAA,CACA,eAAA,CACA,UAAA,CAKN,iCACE,QAAA,CAEA,uCAveJ,YAAA,CACA,kBANY,CAOZ,sBANU,CAOV,kBANQ,CAOR,OANM,CA2BN,UA+csB,CA9ctB,WA8c4B,CA5c5B,iBAAA,CACA,8BAAA,CACA,WAAA,CACA,YAAA,CACA,YAVgB,CAWhB,OAAA,CAEA,oEAAA,CACA,cAAA,CAqcM,UAAA,CAncN,yCACE,aAAA,CACA,UAgcgC,CA/bhC,gBAAA,CAGF,6CACE,+BA2bqC,CA1brC,YAAA,CAGF,8CACE,QAAA,CA4bF,iBACE,6BAAA,CACA,aAAA,CACA,UAAA,CACA,4CAAA,CAIJ,0CAEI,eACE,cAAA,CAGF,aACE,uBAAA,CAGF,kBACE,4BAAA,CACA,WAAA,CAEA,kBAAA,CACA,sCAAA,CACA,iBAAA,CACA,sBAAA,CACA,aAAA,CACA,iBAAA,CAGA,iCACE,gBAAA,CAEA,iBAAA,CACA,UAAA,CACA,SAAA,CACA,WAAA,CACA,YAAA,CAEA,oCACE,iBAAA,CACA,UAAA,CACA,SAAA,CAEA,cAAA,CACA,gBAAA,CAEA,iBAAA,CACA,kBAAA,CAGF,qCACE,WAAA,CACA,YAAA,CAGF,wCACE,aAAA,CACA,WAAA,CACA,YAAA,CACA,UAAA,CAIJ,mCACE,gBAAA,CACA,kBAAA,CAEA,4CACE,iBAAA,CACA,kBAAA,CACA,gBAAA,CACA,iBAAA,CAGF,gDACE,cAAA,CACA,eAAA,CAIJ,gCACE,YAAA,CAIJ,4BACE,QAAA,CAGF,oBACE,WAAA,CACA,aAAA,CACA,oEAAA,CAGF,mBACE,QAAA,CACA,kBAAA,CAGF,cACE,aAAA,CAGF,eACE,kBAAA,CACA,kBAAA,CACA,sBAAA,CACA,QAAA,CACA,YAAA,CAEA,yBACE,SAAA,CACA,eAAA,CAGF,0CAEE,cAAA,CAGF,qBACE,aAAA,CAGF,6BACE,gBAAA,CAEA,mCA/mBN,YAAA,CACA,kBANY,CAOZ,sBANU,CAOV,kBANQ,CAOR,OANM,CA2BN,UAulBwB,CAtlBxB,WAslB8B,CAplB9B,iBAAA,CACA,8BAAA,CACA,WAAA,CACA,YAAA,CACA,YAVgB,CAWhB,OAAA,CAEA,oEAAA,CACA,cAAA,CAEA,qCACE,aAAA,CACA,UAwkBkC,CAvkBlC,gBAAA,CAGF,yCACE,+BAmkBuC,CAlkBvC,YAAA,CAGF,0CACE,QAAA,CAkkBE,uBACE,gBAAA,CACA,MAAA,CACA,YAAA,CAEA,mDAAA,CACA,iBAAA,CAEA,SAAA,CAAA,CAMR,2CAEI,kBACE,cAAA,CACA,eAAA,CAEA,iCACE,WAAA,CACA,YAAA,CACA,iBAAA,CACA,SAAA,CACA,OAAA,CAEA,oBAAA,CAEA,wCACE,YAAA,CAGF,oCACE,QAAA,CACA,SAAA,CACA,QAAA,CAEA,cAAA,CAGF,qCACE,WAAA,CACA,YAAA,CAIJ,8BACE,gBAAA,CAEA,YAAA,CACA,6BAAA,CAEA,2BAAA,CACA,eAAA,CACA,SAAA,CACA,eAAA,CAEA,iBAAA,CACA,QAAA,CACA,4BAAA,CAGF,mCACE,YAAA,CACA,QAAA,CAEA,4CACE,eAAA,CACA,gBAAA,CAGF,gDACE,cAAA,CAIJ,gCACE,YAAA,CACA,iBAAA,CACA,UAAA,CACA,SAAA,CAAA","sourcesContent":["@use 'sass:map';\n\n@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400..700&display=swap');\n@import url('https://fonts.googleapis.com/css2?family=Crimson+Text:ital,wght@0,400;0,600;0,700;1,400;1,600;1,700&family=Dancing+Script:wght@400..700&display=swap');\n\n@font-face {\n  font-family: 'Carnevalee Freakshow';\n  src: url('assets/fonts/Carnevalee Freakshow.ttf');\n}\n\n@mixin flex-container(\n  $direction: row,\n  $justify: center,\n  $align: center,\n  $gap: 0px\n) {\n  display: flex;\n  flex-direction: $direction;\n  justify-content: $justify;\n  align-items: $align;\n  gap: $gap;\n}\n\n@mixin box-shadow(\n  $color: #000000aa,\n  $top: 0,\n  $left: 0,\n  $blur: 10px,\n  $spread: 0px\n) {\n  box-shadow: $top $left $blur $spread $color;\n}\n\n@mixin button(\n  $width: 24px,\n  $height: 24px,\n  $image-size: 80%,\n  $hover-bg: #00000033,\n  $start-opacity: 100%\n) {\n  @include flex-container;\n  width: $width;\n  height: $height;\n\n  border-radius: 8px;\n  background-color: transparent;\n  border: none;\n  outline: none;\n  opacity: $start-opacity;\n  scale: 1;\n\n  transition: background-color 0.3s ease, scale 0.3s ease, opacity 0.3s ease;\n  cursor: pointer;\n\n  & > * {\n    display: block;\n    height: $image-size;\n    font-size: calc($height * ($image-size / 100%));\n  }\n\n  &:hover {\n    background-color: $hover-bg;\n    opacity: 100%;\n  }\n\n  &:active {\n    scale: 0.9;\n  }\n}\n\n* {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n  text-rendering: optimizeLegibility;\n}\n\nimg,\n.material-symbols-outlined {\n  -webkit-user-drag: none;\n  user-select: none;\n  -moz-user-select: none;\n  -webkit-user-select: none;\n  -ms-user-select: none;\n}\n\nbutton {\n  -webkit-tap-highlight-color: transparent;\n}\n\nbody {\n  font-family: 'Crimson Text', sans-serif;\n  min-height: 100vh;\n  min-width: 350px;\n  overflow-x: hidden;\n  background-color: #fefefe;\n\n  @include flex-container(column, start, stretch);\n  position: relative;\n}\n\n.heading {\n  font-family: 'Carnevalee Freakshow', serif;\n  font-size: 64px;\n  line-height: 1.15;\n  font-style: normal;\n  font-weight: 400;\n  font-weight: normal;\n  color: #542851;\n\n  // word-wrap: break-word;\n  // word-break: break-all;\n\n  flex: none;\n}\n\n.social-links {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  gap: 8px;\n\n  flex: none;\n\n  &__icon {\n    @include button(40px, 40px, 70%, rgba(black, 0.1));\n\n    flex: none;\n  }\n}\n\n.about {\n  @include flex-container(column, start, center);\n  padding: 32px 24px;\n  gap: 12px;\n\n  .about__bg {\n    position: absolute;\n    z-index: -1;\n    top: 0;\n    left: 0;\n    right: 0;\n\n    height: 588.5px;\n    filter: drop-shadow(0 4px 4px rgba(black, 0.5));\n\n    &__overlay,\n    &__img {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n      clip-path: polygon(0% 0%, 0% 100%, 100% 69%, 100% 0%);\n    }\n\n    &__overlay {\n      background-color: #5a064b;\n      opacity: 0.6;\n    }\n\n    &__img {\n      opacity: 0.3;\n      object-fit: cover;\n      object-position: top left;\n    }\n  }\n\n  .about__box {\n    @include flex-container(column, start, center);\n    gap: 32px;\n\n    .about__avatar {\n      position: relative;\n      width: clamp(285px, calc(100vw - 2 * 24px), 354px);\n      height: 499px;\n\n      &--ghost {\n        display: none;\n      }\n\n      &__bg {\n        width: 100%;\n        height: 100%;\n        background: radial-gradient(50% 50% at 50% 50%, #3d558a 0%, #1c2c50 100%);\n        box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.5);\n        border-radius: 16px;\n        overflow: hidden;\n        position: relative;\n      }\n\n      img {\n        width: 284.62px;\n        max-width: 90%;\n        height: auto;\n        object-fit: contain;\n\n        position: absolute;\n        bottom: -3px;\n        right: -3px;\n\n        filter: drop-shadow(0px 0px 45.6px rgba(245, 239, 168, 0.3));\n      }\n\n      h1 {\n        position: absolute;\n        width: 300px;\n        height: 168px;\n        top: 326px;\n        left: 18px;\n        z-index: 1;\n\n        font-family: 'Dancing Script';\n        font-style: normal;\n        font-weight: 500;\n        font-size: 72px;\n        line-height: 1.17;\n\n        color: #ffffff;\n\n        filter: drop-shadow(0 0 2px rgba(black, 1));\n        text-shadow: 0px 0px 12px rgba(0, 0, 0, 1);\n      }\n    }\n\n    .about__text-box {\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      padding: 0px;\n      gap: 16px;\n\n      flex: none;\n      align-self: stretch;\n\n      .about__text {\n        font-family: 'Crimson Text';\n        font-style: normal;\n        font-weight: 600;\n        font-size: 28px;\n        line-height: 1.29;\n        color: #777777;\n\n        flex: none;\n        align-self: stretch;\n      }\n    }\n  }\n}\n\n.participants-section {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  padding: 36px 24px 192px;\n  gap: 24px;\n\n  flex: 1 0;\n  align-self: stretch;\n\n  .participants {\n    display: grid;\n    grid-template-columns: repeat(auto-fit, minmax(382px, max-content));\n    justify-content: center;\n    justify-items: center;\n\n    padding: 0px;\n    gap: 84px;\n\n    flex: none;\n    align-self: stretch;\n\n\n    .participant {\n      --width: clamp(250px, calc(100vw - 2 * 24px), 382px);\n      --height: 537px;\n\n      display: grid;\n      justify-content: stretch;\n      align-content: stretch;\n      justify-items: center;\n      align-items: center;\n      grid-template:\n        'picture picture' 4fr\n        'name social-links' 1fr\n        'desc desc' 2fr / 2fr 1fr;\n\n      isolation: isolate;\n\n      width: var(--width);\n      height: var(--height);\n\n      background: #fafafa;\n      box-shadow: 0px 0px 24px rgba(0, 0, 0, 0.15);\n      border-radius: 8px;\n      overflow: hidden;\n\n      flex: none;\n\n      &__picture {\n        grid-area: picture;\n        background-color: #777777;\n        justify-self: stretch;\n        align-self: stretch;\n        overflow: hidden;\n\n        @include flex-container(column, end, center);\n\n        img {\n          max-width: 90%;\n          object-fit: contain;\n          filter: drop-shadow(0px 0px 8px rgba(0, 0, 0, 0.25));\n        }\n      }\n\n      &__name {\n        grid-area: name;\n\n        justify-self: start;\n        margin-left: 29px;\n        margin-top: 16px;\n\n        font-family: 'Crimson Text';\n        font-style: normal;\n        font-weight: 600;\n        font-size: 32px;\n        line-height: 1.31;\n\n        color: #333333;\n      }\n\n      .social-links {\n        grid-area: social-links;\n        margin-top: 16px;\n        margin-right: 24px;\n        justify-self: end;\n\n        &__icon {\n          @include button(48px, 48px, 70%, rgba(black, 0.1));\n        }\n      }\n\n      &__description {\n        grid-area: desc;\n\n        padding: 0 29px;\n        align-self: start;\n\n        font-family: 'Crimson Text';\n        font-style: normal;\n        font-weight: 500;\n        font-size: 24px;\n        line-height: 1.29;\n\n        color: #777777;\n      }\n    }\n\n    $participant-picture-info: (\n        'width': 419px,\n        'height': 307px,\n        'background':\n          radial-gradient(\n            50% 50% at 50% 50%,\n            rgba(228, 172, 106, 0.9) 0%,\n            rgba(184, 127, 60, 0.9) 100%\n          ),\n      ),\n      (\n        'width': 204px,\n        'height': 285px,\n        'background':\n          radial-gradient(\n            50% 50% at 50% 50%,\n            rgba(160, 155, 216, 0.9) 0%,\n            rgba(101, 97, 150, 0.9) 100%\n          ),\n      ),\n      (\n        'width': 200px,\n        'height': 299px,\n        'background':\n          radial-gradient(\n            50% 50% at 50% 50%,\n            rgba(238, 141, 141, 0.9) 0%,\n            rgba(181, 84, 84, 0.9) 100%\n          ),\n      ),\n      (\n        'width': 176.5px,\n        'height': auto,\n        'background':\n          radial-gradient(\n            50% 50% at 50% 50%,\n            rgba(125, 187, 103, 0.9) 0%,\n            rgba(85, 129, 70, 0.9) 100%\n          ),\n      ),\n      (\n        'width': 218px,\n        'height': 277px,\n        'background':\n          radial-gradient(\n            50% 50% at 50% 50%,\n            rgba(70, 146, 217, 0.9) 0%,\n            rgba(51, 96, 137, 0.9) 100%\n          ),\n      ),\n      (\n        'width': 205.5px,\n        'height': auto,\n        'background':\n          radial-gradient(\n            50% 50% at 50% 50%,\n            rgba(170, 128, 175, 0.9) 0%,\n            rgba(122, 59, 131, 0.9) 100%\n          ),\n      );\n\n    @for $i from 1 through length($participant-picture-info) {\n      $info: nth($participant-picture-info, $i);\n\n      .participant:nth-child(#{$i}) .participant__picture {\n        background: map.get($info, 'background');\n\n        img {\n          width: map.get($info, 'width');\n          height: map.get($info, 'height');\n        }\n      }\n    }\n  }\n}\n\n.contact {\n  @include flex-container(column, start, stretch);\n  padding: 24px 0px 0px;\n  gap: 24px;\n\n  background: radial-gradient(50% 50% at 50% 50%, #83487B 0%, #733E6C 100%);\n  box-shadow: 0px -4px 12px 1px rgba(0, 0, 0, 0.1);\n\n  &__contents {\n    @include flex-container(column, start, center);\n    padding: 10px 36px 48px;\n    gap: 24px;\n    flex: 1 1 auto;\n\n    color: #EEEEEE;\n\n    .heading {\n      color: #EEEEEE;\n    }\n\n    .contact__text {\n      /* contact__text */\n\n      font-family: 'Crimson Text';\n      font-style: normal;\n      font-weight: 600;\n      font-size: 28px;\n      line-height: 1.28;\n\n      color: #CCCCCC;\n\n      flex: none;\n    }\n\n    .contact__pairs {\n      @include flex-container(column, start, start);\n      padding: 0px 16px;\n      margin: 8px 0;\n      gap: 16px;\n\n      flex: none;\n\n      .contact__pair {\n        @include flex-container(row, start, center);\n        gap: 10px;\n\n        .contact__icon {\n          display: none;\n          font-size: 32px;\n          color: #DDDDDD;\n        }\n\n        .contact__info {\n          font-family: 'Crimson Text';\n          font-style: normal;\n          font-weight: 700;\n          font-size: 22px;\n          line-height: 1.4;\n          color: #DDDDDD;\n        }\n      }\n    }\n\n    .social-links {\n      gap: 16px;\n\n      &__icon {\n        @include button(64px, 64px, 70%, rgba(black, 0.1));\n        color: #FFFFFF;\n      }\n    }\n  }\n\n  &__avatar {\n    object-position: center center;\n    display: block;\n    width: 100%;\n    box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);\n  }\n}\n\n@media only screen and (min-width: 600px) {\n  #body {\n    .heading {\n      font-size: 40px;\n    }\n\n    .about {\n      padding: 128px 56px 72px;\n    }\n\n    .about__box {\n      width: min(calc(100% - 2 * 24px), 588px);\n      height: auto;\n\n      background: #FAFAFA;\n      box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.25);\n      border-radius: 8px;\n      padding: 30px 36px 48px;\n      display: block;\n      position: relative;\n\n      \n      .about__avatar {\n        align-self: start;\n\n        position: absolute;\n        left: -34px;\n        top: -66px;\n        width: 300px;\n        height: 300px;\n\n        h1 {\n          position: absolute;\n          left: 252px;\n          top: -13px;\n\n          font-size: 48px;\n          line-height: 1.75;\n\n          width: max-content;\n          height: min-content;\n        }\n\n        img {\n          width: 184px;\n          height: 282px;\n        }\n\n        &--ghost {\n          display: block;\n          width: calc(300px - 34px - 36px + 12px);\n          height: calc(300px - 66px - 24px);\n          float: left;\n        }